.Input{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;

}

.Label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;

}

.InputElement {
    background-color: white;
    outline: none;
    border: 1px solid #ccc;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;
}

.InputElement:focus{
    outline: none;
    background-color: rgb(236, 232, 232);
}

.Invalid {
    border-color: red;
    background-color: salmon;
}