.NavigationItem{
    margin: 10px 0;
    box-sizing: border-box;
    height: 100%;
}

.NavigationItem a{
    color: #8f5c2c;
    text-decoration: none;
    height: 100%;
    box-sizing: border-box;
    display: block;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: rgb(15, 68, 129);
}

@media(min-width: 500px){
    .NavigationItem{
        margin: 0%;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    
    .NavigationItem a{
        color: white;
        height: 100%00;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        background-color: #8f5c2c;
        border-bottom: 4px solid black;
        color: white;
    }
}